

/* Navbar */
.navbar {
    display: none;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 2.5rem;
    position: absolute;
    z-index: 1;
    width: 100%;
  }
  
  .navbar-sticky {
    display: flex;
    /* background: #333; */
    /* background-image: url("../assests/45-degree-fabric-light.png");
     */
  background-color: rgb(107, 102, 102);
  color: white;
  
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 1px 1px 1px #222;
    animation: moveDown 0.5s ease-in-out;
    /* height: 60px; */
    /* margin-left: 685px; */
  }
  
  .navbar--logo-holder {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .navbar--logo {
    width: 2rem;
    height: 2rem;
    margin-right: 0.5rem;
    animation: rotate 0.7s ease-in-out 0.5s;
    /* background-color: #fff; */
  }
  
  .navbar--link {
    display: flex;
    list-style: none;
  }
  
  .navbar--link-item {
    margin: 0.4rem 1rem 0 0;
    padding: 0 0.3rem;
    cursor: pointer;
    
  }
  
  .navbar--link-item:hover{
    font-size: 16.5px;
  }
  @keyframes moveDown {
    from {
      transform: translateY(-5rem);
    }
    to {
      transform: translateY(0rem);
    }
  }
  
  @keyframes rotate {
    0% {
      transform: rotateY(360deg);
    }
    100% {
      transform: rotateY(0rem);
    }
  }
  /* Welcome */
  .welcome {
    /* background-color: #1f1f1f; */
    background-attachment: fixed;
    /* background-image: url("../../assests/45-degree-fabric-light.png"); */
    /* background-size: cover; */
    line-height: 1.8;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .welcome--logo {
    width: 15rem;
    margin: auto;
    animation: rotate 0.7s ease-in-out 0.5s;
  
  
    /* background-color: #e74c3c; */
    /* border-radius: 50px; */
  }
  
  .welcome--about {
    padding: 2rem;
    width: 50rem;
    color: #444;
    text-align: justify;
    margin: auto;
  }
  
  .welcome__cta-primary {
    /* background: #e74c3c; */
    color:white;
    margin-top: 1rem;
    padding: 0.7rem 1.5rem;
    border: none;
    border-radius: 20px;
    font-weight: 700;
    background-color: rgb(12, 168, 168);
  }
  .name{
    font-size: 16px;
    animation: rotate 0.7s ease-in-out 0.5s;
  
  }
  
  @media screen and (max-width: 600px){
    .navbar-sticky{
      display: none;
    }
  }
  
  /* medium sized devices */
  @media screen and (max-width: 1024px){
    .navbar-sticky{
      display: none;
    }
  }
  
  